// src/router.jsx 
import { createHashRouter, RouterProvider, Outlet, Link, useLocation } from 'react-router-dom';
import MusicPlayer from './MusicPlayer';
import { Layout, Menu } from 'antd';
import { AudioOutlined, InfoCircleOutlined } from '@ant-design/icons';

// LayoutMenu
const RootLayout = () => {
    const { Header, Content } = Layout;
    const location = useLocation(); // 
    const menuItems = [
        {
            key: '/',
            icon: <AudioOutlined />,
            label: <Link to="/"></Link>,
        },
        {
            key: '/about',
            icon: <InfoCircleOutlined />,
            label: <Link to="/about"></Link>,
        },
    ];

    return (
        <Layout style={{ minHeight: '100vh' }}>
            <Header style={{ background: '#fff', padding: '0 20px' }}>
                {/*  */}
                <Menu mode="horizontal" selectedKeys={[location.pathname]} items={menuItems} />
            </Header>
            <Content style={{ padding: '20px', display: 'flex', justifyContent: 'center' }}>
                <Outlet />
            </Content>
        </Layout>
    );
};

//   - React Router v6.4+
const ErrorPage = () => (
    <div style={{ textAlign: 'center', marginTop: '80px', padding: '20px', color: '#666' }}>
        <h2 style={{ color: '#c71585', fontSize: '20px' }}> </h2>
        <p style={{ marginTop: '20px' }}>...</p>
        <p style={{ marginTop: '10px', fontSize: '12px' }}> </p>
    </div>
);

//  
const router = createHashRouter(
    [
        {
            path: '/',
            element: <RootLayout />,
            errorElement: <ErrorPage />, //  
            children: [
                { path: '/', element: <MusicPlayer /> }, // 
                {
                    path: '/about',
                    element: (
                        <div style={{ maxWidth: 700, margin: '0 auto' }}>
                            <h2 style={{ textAlign: 'center', color: '#c71585', marginTop: 20 }}>
                                 React 
                            </h2>
                            <p style={{ textAlign: 'center', color: '#666', margin: '10px auto', lineHeight: '1.6' }}>
                                <strong></strong>
                            </p>
                            <p style={{ textAlign: 'center', color: '#666', margin: '10px auto', lineHeight: '1.6' }}>
                                <strong></strong> React HooksuseState/useRef/useEffect Ant Design  JavaScript  API 
                            </p>
                            <p style={{ textAlign: 'center', color: '#666', margin: '10px auto', lineHeight: '1.6' }}>
                                <strong></strong> React  Hooks 
                            </p>
                        </div>
                    )
                },
                { path: '*', element: <ErrorPage /> } //  
            ]
        },
        { path: '*', element: <ErrorPage /> } //  404
    ],
);

// 
const AppRouter = () => <RouterProvider router={router} />;
export default AppRouter;